div.tfw-view-InputFile {
    padding: 0;
    margin: .25rem;
    display: inline-block;
}
div.tfw-view-InputFile.wide {
    display: block;
}
div.tfw-view-InputFile > input {
    display: none;
}

div.tfw-view-touchable.enabled {
  cursor: pointer;
}
.tfw-view-Expand {
    display: block;
    padding: 0 16px;
    height: 32px;
}
.tfw-view-Expand.expand {
    height: auto;
}

.tfw-view-Expand > .head {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: start;
            justify-content: start;
    -webkit-align-items: center;
            align-items: center;
    height: 32px;
    font-family: Roboto, josefin-sans, sans-serif;
    font-size: 14px;
    font-weight: bold;
    -webkit-font-feature-settings: "c2sc", "liga", "clig", "c2sc";
            font-feature-settings: "c2sc", "liga", "clig", "c2sc";
    font-variant: small-caps;
    font-variant: common-ligatures small-caps;
    cursor: pointer;
}

.tfw-view-Expand > .head > div.icons {
    position: relative;
    width: 24px;
    height: 24px;
    padding: 0;
    margin: 0 8px 0 0;
}

.tfw-view-Expand > .head > div.icons > * {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    transition: all .3s;
}

.tfw-view-Expand > .head > div.icons > *:first-child {
    -webkit-transform: rotate(0);
            transform: rotate(0);
    opacity: 1;
}
.tfw-view-Expand > .head > div.icons > *:last-child {
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
    opacity: 0;
}

.tfw-view-Expand.expand > .head > div.icons > *:first-child {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    opacity: 0;
}
.tfw-view-Expand.expand > .head > div.icons > *:last-child {
    -webkit-transform: rotate(0);
            transform: rotate(0);
    opacity: 1;
}


.tfw-view-Expand > div.body {
    display: block;
    visibility: collapse;
    padding: 0 0 0 16px;
    margin: 0;
    transition: none;
    -webkit-transform: translateX(100px);
            transform: translateX(100px);
    opacity: 0;
}

.tfw-view-Expand.expand > div.body {
    display: block;
    visibility: visible;
    transition: all .2s;
    -webkit-transform: translateX(0);
            transform: translateX(0);
    opacity: 1;
}

